.profile_row {
  padding: 0 12px;
}
.row .profile_content {
  background: rgba(255, 255, 255, 0.01);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(50px);
  border-radius: 30px;

  position: relative;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 65px - 35px);
  padding: 1px;
  margin: 50px 0;
}
.profile_menu {
  display: flex;
  margin-left: 20px;
  margin-top: 20px;
}
.profile_menu_item {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  min-width: 140px;
  min-height: 35px;
  padding: 0 10px;
  font-weight: 600;
  font-size: 20px;
  line-height: 23px;
  transition: 0.2s;
  background: #222831;
  border-radius: 30px;
  color: #fff;
}
.profile_menu_item:not(:last-child) {
  margin-right: 15px;
}
.profile_menu_item-active {
  background: var(--color-bg-grad-1);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
  border-radius: 30px;
}
.profile_menu_item-isGame {
  width: 8px;
  height: 8px;
  background: var(--color-bg-grad-4);
  box-shadow: 0px 0px 3px rgba(247, 37, 133, 0.5);
  border-radius: 50%;
  margin-left: 10px;
}
