.login_contaier {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 65px - 35px);
}

.login_panel {
  height: 400px;
  position: relative;
}
.login_panel_content {
  position: relative;
  height: 400px;
  background: rgba(255, 255, 255, 0.01);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(50px);
  border-radius: 30px;
  z-index: 50;
  padding: 1px;
}
.login_panel:before {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  position: absolute;
  background: linear-gradient(94.67deg, var(--color-bg-grad-4) 0%, var(--color-bg-grad-3) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-2);
  transform: rotate(74.85deg);
  border-radius: 100px;
  z-index: -1;
  top: -15px;
  left: 22px;
}
.login_panel:after {
  content: '';
  display: block;
  width: 156px;
  height: 156px;
  position: absolute;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  transform: rotate(74.85deg);
  border-radius: 100px;
  z-index: -1;
  bottom: -70px;
  left: -35px;
}
.login_panel_content_title {
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 23px;
  color: var(--text-color);
  margin: 0;
  margin-top: 10px;
}
.login_panel_content_inputs {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login_panel_content_input {
  padding: 1rem 25px;
  background: var(--input-bg);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(50px);
  border-radius: 30px;
  color: var(--text-color);
  font-weight: 500;
}
.login_panel_content_input:not(:first-child) {
  margin-top: 35px;
}
.login_panel_content_input::placeholder {
  color: var(--text-color);
  font-weight: 600;
}
.login_panel_content_btns {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
}
.login_panel_content_btn {
  background: linear-gradient(90deg, var(--color-bg-grad-2) 0%, var(--color-bg-grad-1) 100%);
  mix-blend-mode: normal;
  box-shadow: 0px 0px 50px var(--color-light-btn);
  border-radius: 5px;
  color: #ffffff;
  height: 40px;
  cursor: pointer;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
}
