*,
:after,
:before {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
p {
  margin: 0;
}
button,
input {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}
a {
  color: var(--text-color);
  text-decoration: none;
}
a:hover {
  color: var(--color-light-btn);
  text-shadow: 0px 0px 25px var(--color-light-btn);
}

.dark-theme {
  --color-bg-grad-1: #4895ef;
  --color-light-btn: #4cc9f0;
  --color-bg-shadow-1: rgba(72, 150, 239, 0.68);
  --color-bg-shadow-2: rgba(247, 37, 132, 0.68);
  --color-bg-grad-2: #3f37c9;

  --color-bg-grad-3: #b5179e;
  --color-bg-grad-4: #f72585;

  --bg-color: linear-gradient(165.14deg, #393e46 0%, #222831 98.33%);
  --bg: url('../img/night_1.png');
  --man-px: -310px;
  --text-color: #e3e3e3;
  --inversion-text-color: #222831;

  --header-text-color: #e3e3e3;

  --border-btn: 1px solid var(--color-bg-grad-2);
  --shadow-btn: 0px 0px 25px #7209b7;
  --input-bg: #222831;
}
.light-theme {
  --color-bg-grad-4: #4895ef;
  --color-bg-shadow-2: rgba(72, 150, 239, 0.68);
  --color-bg-shadow-1: rgba(247, 37, 132, 0.68);

  --color-light-btn: #f72585;

  --color-bg-grad-3: #3f37c9;

  --color-bg-grad-2: #b5179e;
  --color-bg-grad-1: #f72585;

  --bg-color: #f9f9f9;
  --bg: url('../img/day_1.png');
  --man-px: -326px;
  --text-color: #222831;
  --inversion-text-color: #e3e3e3;

  --header-text-color: #f9f9f9;

  --border-btn: none;
  --shadow-btn: 0px 0px 25px #b5179d;
  --input-bg: #f9f9f9;
}
.wrapper {
  min-height: 100vh;
  color: var(--text-color);
  background: var(--bg-color);
  display: flex;
  flex-direction: column;
  font-family: 'Raleway', sans-serif;
}

.container {
  margin: 0 auto;
  z-index: 15;
}
.header {
  background: var(--bg);
  min-height: 877px;
  background-size: cover;
  position: relative;
}
.header_container {
  position: relative;
}

.nav_bg_shadow {
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  filter: blur(70px);
  height: 60px;
  position: absolute;
  margin: 0 auto;
  z-index: 1;
}
.header_nav_elements {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
}

.header_menu {
  display: flex;
  list-style: none;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
}
.header_menu_item:not(:last-child) {
  margin-right: 50px;
}
.header_menu_link {
  text-decoration: none;
  transition: 0.2s;
  color: var(--header-text-color);
}
.header_menu_link:hover {
  font-weight: bold;
  color: var(--color-light-btn);
  text-shadow: 0px 0px 15px var(--color-light-btn);
}

.header_btn {
  cursor: pointer;
  border: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  padding: 12px;
  transition: 0.5s;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  mix-blend-mode: normal;
  border: var(--border-btn);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  border-radius: 5px;

  color: var(--header-text-color);
}
.header_btn a {
  text-decoration: none;
}
.header_btn a:hover {
  text-shadow: none;
  color: var(--header-text-color);
}
.header_btn:hover {
  box-shadow: var(--shadow-btn);
}
.nav_btn_auth {
  display: inline-flex;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
}
.nav_btn_auth:not(:last-child) {
  margin-right: 20px;
}
.nav_btn_auth:hover svg path {
  fill: var(--color-light-btn);
  transition: 0.2s;
}
.header_hamburger {
  display: none;
}

.header_hamburger {
  width: 40px;
  position: relative;
  height: 30px;
  cursor: pointer;
}
.header_hamburger_line:before,
.header_hamburger_line:after {
  content: '';
  display: block;
}
.header_hamburger_line,
.header_hamburger_line:before,
.header_hamburger_line:after {
  width: 40px;
  height: 4px;
  background: #fff;
  border-radius: 4px;
  position: absolute;
  top: auto;
  bottom: 0;
}
.header_hamburger_line:before {
  top: -10px;
}
.header_hamburger_line:after {
  top: -20px;
}

.header_bg_hamburger_menu {
  transition: 0.4s;
  display: flex;
  width: 300px;
  height: 100%;
  background: rgba(0, 0, 0, 0.336);
  z-index: 50;
  position: fixed;
  margin: 0;
  padding: 0;
  right: -400px;
  top: 0;
}
.header_hamburger_menu {
  margin: 0;
  padding: 0;
  list-style: none;
}
.header_bg_hamburger_menu {
  transition: 0.4s;
  display: flex;
  width: 300px;
  height: 100%;
  background: linear-gradient(
    165.14deg,
    rgba(57, 62, 70, 0.98) -3.18%,
    rgba(34, 40, 49, 0.98) 95.16%
  );
  border-radius: 10px 0px 0px 10px;
  z-index: 50;
  position: fixed;
  margin: 0;
  right: -400px;
  top: 0;
}
.header_hamburger_menu {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
}

.header_hamburger_menu_item {
  position: relative;
  padding: 15px 30px;
  border-bottom: 2px solid #393e46;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: var(--header-text-color);
}
.header_hamburger_menu_item:hover {
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
}

.header_hamburger_menu_title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding: 30px 30px 0 30px;
}

.header_hamburger_menu_title h2 {
  font-weight: 600;
  font-size: 25px;
  line-height: 29px;
  margin: 0;
}
.man {
  position: absolute;
  right: 0;
  bottom: var(--man-px);
}

.header_content {
  margin-top: 195px;
}

.header_content_slogn {
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  color: var(--header-text-color);
}

.header_content_online {
  margin-top: 30px;
  width: 150px;
  height: 40px;
  background: rgba(210, 210, 210, 0.5);
  border-radius: 15px;
  padding: 0 20px;
}

.header_content_online p {
  font-size: 20px;
  line-height: 23px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: var(--header-text-color);
}

.header_content_text {
  margin: 0;
  font-weight: 300;
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.15em;
  margin-top: 5px;
  color: var(--header-text-color);
}

.header_content_button-play {
  width: 385px;
  height: 75px;
  background: rgba(0, 0, 0, 0);
  border: 3px solid var(--color-bg-grad-1);
  box-sizing: border-box;
  box-shadow: 0px 0px 15px var(--color-bg-grad-1);
  border-top-left-radius: 100px;
  border-bottom-right-radius: 100px;

  cursor: pointer;
  padding: 0 35px;
  margin-top: 15px;

  font-weight: 500;
  font-size: 25px;
  line-height: 29px;
  color: var(--header-text-color);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  text-decoration: none;
}
.header_content_button-play svg {
  transition: 0.3s;
}

.header_content_button-play:hover {
  color: var(--header-text-color);
  text-shadow: none;
}

.header_content_button-play:hover svg {
  fill: var(--color-light-btn);
}

.isLogin {
  display: none;
}
.playnow {
  margin-top: 50px;
}
.playnow .row {
  position: relative;
  margin-top: 60px;
}
.playnow_title {
  font-weight: 600;
  font-size: 50px;
  line-height: 59px;
  text-align: center;
}

.playnow_step {
  position: relative;
  max-width: 300px;
  min-height: 250px;
  z-index: 5;
}

.playnow_step_content {
  width: 100%;
  padding: 10px 10px;
  background: rgba(34, 40, 49, 0.01);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(30px);
  border-radius: 30px;
}
.playnow_step_content_title {
  font-weight: 600;
  font-size: 20px;
  line-height: 23px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  text-align: center;
}
.playnow_step_one:before {
  content: '';
  display: block;
  width: 156px;
  height: 156px;
  position: absolute;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  transform: rotate(74.85deg);
  border-radius: 100px;
  z-index: -1;
  left: -65px;
  top: -15px;
}
.playnow_step_one:after {
  content: '';
  display: block;
  position: absolute;
  width: 45px;
  height: 45px;

  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);

  border-radius: 100px;
  z-index: -1;
  bottom: 0;
  right: 70px;
}
.playnow_step_content_one {
  height: 300px;
}
.playnow_step_one_title {
  font-weight: 300;
  font-size: 28px;
  width: 100%;
  line-height: 33px;
  display: inline-block;
  text-align: center;
  font-feature-settings: 'pnum' on, 'lnum' on;
  position: absolute;
  z-index: 5;
  top: -33px;
  text-align: center;
}

.playnow_step_content_one_title {
  margin-top: 35px;
}
.playnow_step_content_one_text {
  font-size: 15px;
  line-height: 18px;
  margin-top: 20px;
  padding: 0 15px;
}
.playnow_step_content_one_links {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.playnow_step_content_one_link_steam {
  margin-right: 35px;
  transition: 0.3s;
}
.playnow_step_content_one_link_epicgames {
  transition: 0.3s;
}
.playnow_step_content_one_link_epicgames:hover,
.playnow_step_content_one_link_steam:hover {
  transform: scale(1.1);
}

.playnow_step_two:before {
  display: block;
  position: absolute;
  content: '';
  width: 45px;
  height: 45px;
  background: linear-gradient(90deg, var(--color-bg-grad-3) 0%, var(--color-bg-grad-4) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-2);
  transform: rotate(126.98deg);
  border-radius: 50%;
  top: -15px;
  left: 5px;
}
.playnow_step_two:after {
  content: '';
  display: block;
  position: absolute;
  right: -40px;
  bottom: -60px;
  border-radius: 50%;
  width: 156px;
  height: 156px;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  transform: rotate(113.92deg);
  z-index: -1;
}
.playnow_step_two_title {
  font-weight: 300;
  font-size: 28px;
  width: 100%;
  line-height: 33px;
  display: inline-block;
  text-align: center;
  font-feature-settings: 'pnum' on, 'lnum' on;
  position: absolute;
  z-index: 5;
  bottom: -33px;
  text-align: center;
}
.playnow_step_content_two {
  height: 322px;
}
.playnow_step_content_two_title {
  margin-top: 65px;
}
.playnow_step_two_content_link_ragemp {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 65px;
  top: 0;
  transition: 0.3s;
}
.playnow_step_two_content_link_ragemp:hover {
  top: -5px;
}

/*   */
.playnow_step_three {
  height: 250px;
}
.playnow_step_three:before {
  display: block;
  position: absolute;
  content: '';
  width: 60px;
  height: 60px;
  background: linear-gradient(90deg, var(--color-bg-grad-3) 0%, var(--color-bg-grad-4) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-2);
  transform: rotate(-146.31deg);
  border-radius: 50%;
  top: 35%;
  left: -25px;
}

.playnow_step_three:after {
  content: '';
  display: block;
  position: absolute;
  width: 183px;
  height: 183px;
  border-radius: 50%;
  right: -45px;
  top: -10px;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  transform: rotate(-55.39deg);
  z-index: -1;
}
.playnow_step_content_three_circle {
  display: block;
  position: absolute;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  bottom: -25px;
  right: 20px;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  z-index: -1;
}
.playnow_step_three_title {
  font-weight: 300;
  font-size: 28px;
  width: 100%;
  line-height: 33px;
  display: inline-block;
  text-align: center;
  font-feature-settings: 'pnum' on, 'lnum' on;
  position: absolute;
  z-index: 5;
  top: -33px;
  text-align: center;
}
.playnow_step_content_three {
  height: 250px;
}
.playnow_step_content_title {
  margin-top: 40px;
}
.playnow_step_content_three_text {
  font-weight: 600;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  margin-top: 40px;
}
.playnow_step_content_three_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.playnow_step_content_three_btn button {
  margin-top: 30px;
  position: relative;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%),
    rgba(255, 255, 255, 0.5);
  box-shadow: 0px 0px 10px var(--color-light-btn);
  border-radius: 18.5px;
  padding: 10px;
  text-align: center;
  color: var(--header-text-color);
  cursor: pointer;
  transition: 0.35s;
}
.playnow_step_content_three_btn button:hover {
  background: linear-gradient(94.67deg, var(--color-bg-grad-2) 0%, var(--color-bg-grad-1) 100%),
    rgba(255, 255, 255, 0.5);
  transform: scale(1.1);
}

.monitoring {
  margin-top: 170px;
  margin-bottom: 150px;
}
.server {
  position: relative;
  z-index: 5;
}
.server:before {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;

  width: 59px;
  height: 59px;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  border-radius: 50%;
  left: -5px;
  bottom: -15px;
}
.server:after {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  width: 30px;
  height: 30px;
  background: linear-gradient(94.67deg, var(--color-bg-grad-1) 0%, var(--color-bg-grad-2) 100%);
  box-shadow: 0px 0px 50px var(--color-bg-shadow-1);
  border-radius: 50%;
  right: 15px;
  top: 40px;
}
.server_name {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 30px;
  line-height: 35px;
  display: flex;
  align-items: center;
  text-align: center;
  margin-bottom: 10px;
}
.server_name svg {
  margin-right: 10px;
}
.server_name svg path {
  fill: var(--text-color);
}
.progress_bar {
  position: relative;
  width: 100%;
  height: 59px;
  background: rgba(34, 40, 49, 0.01);
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(50px);
  border-radius: 30px;
  overflow: hidden;
}

.progress_bar_online {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: normal;
  font-size: 30px;
  line-height: 35px;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: -0.05em;
  font-feature-settings: 'pnum' on, 'lnum' on;
  z-index: 3;
}
.progress_bar_line {
  transition: 0.5s;
  position: absolute;
  width: 0%;
  height: 59px;
  background: linear-gradient(90deg, var(--color-bg-grad-3) 0%, var(--color-bg-grad-4) 100%);
  box-shadow: 0px 0px 25px rgba(247, 37, 133, 0.3);
  border-radius: 30px;
}

.footer {
  padding-bottom: 10px;
  margin-top: auto;
}
#vk,
#discord {
  transition: 0.2s;
}
#vk path,
#discord path {
  transition: 0.2s;
  fill: var(--text-color);
}
#vk:hover,
#discord:hover {
  -webkit-filter: drop-shadow(0px 0px 3px var(--color-bg-grad-1));
  filter: drop-shadow(0px 0px 3px var(--color-bg-grad-1));
  transform: scale(1.2);
}
.footer_link {
  font-weight: 600;
  font-size: 20px;
  line-height: 23px;
  text-decoration: none;
  transition: 0.2s;
}
@media (max-width: 1090px) {
  .header_menu {
    display: none;
  }
  .header_btn {
    display: none;
  }
  .nav_btn_auth {
    display: none;
  }
  .header_hamburger {
    display: flex;
  }
  .header_content_button-play {
    transform: scale(0.8);
  }
}

@media (max-width: 768px) {
  .header_content_slogn {
    font-size: 30px;
    text-align: center;
    font-weight: 700;
  }
  .playnow_step_one:before {
    width: 78px;
    height: 78px;
    left: -10px;
  }
  .playnow_step_one:after {
    bottom: -25px;
  }
  .playnow_step_two:after {
    width: 78px;
    height: 78px;
    bottom: -5px;
    right: -15px;
  }
  .playnow_step_three:after {
    height: 91px;
    width: 91px;
    right: -15px;
  }
  .playnow_step_one_title {
    top: 0;
  }
  .playnow_step_two_title {
    top: 0;
    bottom: auto;
  }
  .playnow_step_three_title {
    top: 0;
  }

  .social_links {
    flex-direction: column;
    justify-content: space-between;
  }
  .footer_link:not(:first-child) {
    display: block;
    margin-top: 20px;
  }
}

.mt-5 {
  margin-top: 3rem !important;
}
.top_bg_header {
  position: absolute;
  background: linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(0, 212, 255, 0) 100%);
  opacity: 0.3;
  width: 100%;
  height: 80px;
  z-index: 1;
}
